.tile-button-group {
    position: relative;
    display: flex;
    width: 100%;
    // height: 100%;
    align-items: center;
    gap: 12px;
    flex-wrap: wrap;
    padding: 10%;
    // background: rebeccapurple;
    // // justify-content: center;
    // align-items: center;
    // // align-content: center;
    // // align-self: center;
    
    & > * {
        justify-content: center;
        flex: 30%;
    }
}


.tile-button {
    position: relative;
    pointer-events: all;
    // background-color: #000000AA !important;
    border-color: white;
    color: white;
    height: 100px;
    min-width: 100px;

    .tile-button-icon {
        position: absolute;
        
        svg.MuiSvgIcon-root {
            color: white;
            font-size: 3rem;
        }
    }

    .tile-button-label {
        position: absolute;
        color: white;
        left: 7px;
        bottom: 2px;
    }
}