* {
  -webkit-transition: all 300ms ease;
  -moz-transition: all 300ms ease;
  transition: all 300ms ease;
}

.profile-page {
  display: grid;
  position: relative;
  height: 100%;
  width: 100%;
  // color: darkcyan;
  overflow: hidden;
  grid-template-columns: auto;
  grid-template-rows: 1fr 1fr;
  grid-template-areas: 'body' 'nav';
  height: 100vh;

  &.banner-mode {
    overflow: hidden;
  }

  .profile-body {
    grid-area: body;
    position: relative;
    overflow: hidden;
    width: 100%;

    .profile-pic {
      width: 100%;
      display: inline-block;

      & > img {
        overflow: hidden;
        width: 50%;
        margin: 20% 25%;
        border: 2px solid white;
        border-radius: 100%;
      }

      &.banner-mode {
        & > img {
          margin: 0;
          width: 50px;
          margin: 4px;
        }
      }

      .banner-mode {
        width: auto;
      }
    }

    .profile-info {
      position: relative;
      display: inline-block;
      width: calc(100% - 80px);
      padding: 10px;
      margin-left: 40px;
      margin-right: 40px;
      text-align: center;

      &.banner-mode {
        margin-left: 60px;
        margin-right: 0px;
        text-align: left;
      }

      .profile-info-content {
        position: absolute;
        top: -400%;
        left: 0;
        width: 100%;

        &.banner-mode {
          top: -60px;
        }

        &.banner-mode * {
          line-height: 1;
        }
      }
    }
  }

  .profile-nav-area {
    grid-area: nav;
    overflow: hidden;

    &.banner-mode {
      opacity: 0;
    }
  }

  @keyframes profile-page-change {
    0% {
      height: 100%;
    }
    100% {
      height: 58px;
    }
  }
  
  @keyframes profile-body-change {
    0% {
      max-height: 100%;
    }
    100% {
      max-height: 60px;
    }
  }
  
  @keyframes profile-nav-change {
    0% {
      margin-left: 0;
    }
    100% {
      margin-left: 200%;
    }
  }  
}