.desktop-layout {    
    display: grid;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    grid-template-columns: minmax(auto, 370px) auto;
    grid-template-rows: auto 1fr;
    grid-template-areas:
        'p m'
        's m';
    height: 100vh;
    padding-top: 20px;
  
    aside {
        grid-area: p;
        background-color: #30475E;
        width: rem-calc(200px);
    }

    main {
        grid-area: m;
        // padding: 20px;
    }

    .spacer {
        background-color: red;
        height: 100%;
        grid-area: s;
    }
    
    .profile-page {
        background-color: #FFFFFF55;
        border-radius: 20px;
        height: auto;
    }

    .wrapper-root {
        padding-top: 0;
    }
}
